<template>
  <div
    id="calc"
    class="d-flex align-items-center vh-100"
    :class="{ 'accessibility-contrast': contrastMode }"
  >
    <div class="container container--narrow">
      <button @click="toggleContrast()">Toggle Contrast</button>

      <button
        type="button"
        v-b-tooltip.hover
        title="If you're mixing / producing music you want to keep your reverbs, delays
          and sometimes compressor in sync with the tempo of the song. Using a
          millisecond tempo calculator helps you do just that. Insert your tempo
          and the notation you want to get the right milliseconds. Please note
          this is based on 4/4 timing."
      >
        <span aria-hidden>i</span>
        <span class="sr-only">Help tooltip</span>
      </button>

      <!-- Tempo -->
      <div class="row">
        <div class="col">
          <h1>Tempo to Milliseconds Calculator</h1>

          <div class="input-group align-items-center">
            <div class="input-group-prepend">
              <button
                type="button"
                aria-label="Decrease temp by 1"
                class="button-increment"
                @click="increment(-1)"
              >
                <span class="sr-only">Decrease by one</span>
                <span aria-hidden>-</span>
              </button>
            </div>

            <ValidationObserver>
              <ValidationProvider
                rules="min_value:1|required"
                name="Tempo"
                v-slot="{ errors }"
              >
                <label for="tempo" class="input__label">
                  <span class="sr-only">Tempo</span>
                </label>

                <input
                  id="tempo"
                  type="number"
                  class="form-control form-no-spinner"
                  v-model.number="tempo"
                />

                <span>{{ errors[0] }}</span>
              </ValidationProvider>
            </ValidationObserver>

            <div class="input-group-append">
              <span>BPM</span>

              <button
                type="button"
                aria-label="Increase temp by 1"
                class="button-increment"
                @click="increment(1)"
              >
                <span class="sr-only">Increase by one</span>
                <span aria-hidden>+</span>
              </button>
            </div>
          </div>
        </div>
      </div>

      <!-- Note Options -->
      <div class="row my-5">
        <div class="col">
          <div class="d-none d-md-block">
            <div class="row">
              <div
                v-for="(modifier, name) in modifiers"
                :key="name"
                class="col"
              >
                <h3>{{ name }}</h3>
                <li
                  v-for="(value, note) of notes"
                  :key="value"
                  class="row px-2"
                >
                  <div class="col-4">
                    {{ note }}
                  </div>
                  <div class="col">
                    {{ displayMeasurement(value, modifier) }}
                    {{ displaySymbol(value, modifier) }}
                  </div>
                </li>
              </div>
            </div>
          </div>

          <div class="d-md-none">
            <template v-for="(modifier, name, index) in modifiers">
              <b-card no-body :key="name">
                <b-card-header header-tag="header" role="tab">
                  <b-button block v-b-toggle="`accordion-${index}`">
                    {{ name }}
                  </b-button>
                </b-card-header>

                <b-collapse
                  :id="`accordion-${index}`"
                  :visible="index === 0"
                  accordion="calcuator"
                  role="tabpanel"
                >
                  <b-card-body body-class="py-3 px-5">
                    <!-- Titles -->
                    <b-card-text>
                      <div class="row">
                        <div class="col-4"><b>Note</b></div>
                        <div class="col"><b>Milliseconds</b></div>
                      </div>
                    </b-card-text>

                    <!-- Measurements -->
                    <b-card-text v-for="(value, note) of notes" :key="value">
                      <div class="row">
                        <div class="col-4">
                          {{ note }}
                        </div>
                        <div class="col">
                          {{ displayMeasurement(value, modifier) }}
                          {{ displaySymbol(value, modifier) }}
                        </div>
                      </div>
                    </b-card-text>
                  </b-card-body>
                </b-collapse>
              </b-card>
            </template>
          </div>
        </div>
      </div>

      <!-- Footer -->
      <div class="row">
        <div class="col text-center">
          <b-img :src="logo" height="40" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ValidationObserver, ValidationProvider, extend } from "vee-validate";
import { min_value, required } from "vee-validate/dist/rules";
import logoWhite from "@/assets/logo-white.svg";
import nightmode from "@/utilities/nightmode";
import logoBlack from "@/assets/logo.svg";

extend("min_value", min_value);
extend("required", required);

export default {
  name: "Calculator",
  components: {
    ValidationObserver,
    ValidationProvider
  },
  data() {
    return {
      contrastMode: false,
      logos: {
        black: logoBlack,
        white: logoWhite
      },
      tempo: 120,
      tempoMin: 40,
      tempoMax: 240,
      modifiers: {
        Standard: 1,
        Triplets: 0.667,
        Dotted: 1.5
      },
      notation: 0.5,
      notes: {
        "1/128": 0.03125,
        "1/64": 0.0625,
        "1/32": 0.125,
        "1/16": 0.25,
        "1/8": 0.5,
        "1/4": 1,
        "1/2": 2,
        "1 bar": 4,
        "2 bars": 8,
        "3 bars": 12,
        "4 bars": 16
      }
    };
  },
  computed: {
    logo() {
      return this.contrastMode ? this.logos.white : this.logos.black;
    }
  },
  mounted() {
    if (nightmode()) this.contrastMode = true;
  },
  methods: {
    displayMeasurement(notation, modifier) {
      return this.milliseconds(notation, modifier) >= 1000
        ? (this.milliseconds(notation, modifier) / 1000).toFixed(2) // Seconds
        : this.milliseconds(notation, modifier).toFixed(0); // Milliseconds
    },
    displaySymbol(notation, modifier) {
      if (this.tempo > 0 && this.milliseconds(notation, modifier) < 1000)
        return "ms";
      return "s";
    },
    milliseconds(notation, modifier) {
      return (60000 / (parseFloat(this.tempo) / notation)) * modifier;
    },
    increment(increment) {
      this.tempo = parseFloat(this.tempo) + increment;
      this.validateTempo();
    },
    toggleContrast() {
      this.contrastMode = !this.contrastMode;
    },
    validateTempo() {
      if (parseFloat(this.tempo) < this.tempoMin) this.tempo = this.tempoMin;
      if (parseFloat(this.tempo) > this.tempoMax) this.tempo = this.tempoMax;
    }
  }
};
</script>
